import axios from "axios";
import React, { useState, useEffect } from "react";
import { toast, ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import ScrollToTop from "./scrolltotop";
import Cookies from "js-cookie";
import CryptoJS from "crypto-js";
import { message } from "antd";

export default function Wallet() {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [balance, setBalance] = useState(0);
  const [transactions, setTransactions] = useState([]);
  const encryptionKey = process.env.REACT_APP_ENCRYPTION_KEY;
  const encryptedUser = Cookies.get("user");
  const user = encryptedUser
    ? JSON.parse(
      CryptoJS.AES.decrypt(encryptedUser, encryptionKey).toString(
        CryptoJS.enc.Utf8
      )
    )
    : null;

  const encryptedToken = Cookies.get("token");
  const token = encryptedToken
    ? CryptoJS.AES.decrypt(encryptedToken, encryptionKey).toString(
      CryptoJS.enc.Utf8
    )
    : null;
  const fetchWalletData = async () => {
    try {
      const response = await axios.get(
        `https://majesticmall.net/api/user/getwallet/${user?._id}`,
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );
      setBalance(response.data.success.balance);
      setTransactions(response.data.success.transactions?.reverse());
    } catch (error) {
      console.error("Error fetching wallet data:", error);
    }
  };
  useEffect(() => {
    if (token) {
      fetchWalletData();
    }
    //  toast.error("Please login.");
  }, [token]);

  const openModal = () => setIsModalOpen(true);
  const closeModal = () => setIsModalOpen(false);
  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 10;
  const currentItems = transactions.slice(
    (currentPage - 1) * itemsPerPage,
    currentPage * itemsPerPage
  );
  // console.log("transactions",transactions);
  const totalPages = Math.ceil(transactions.length / itemsPerPage);

  const getVisiblePages = () => {
    const maxVisible = 5;
    const pages = [];

    if (totalPages <= maxVisible) {
      return [...Array(totalPages).keys()].map((n) => n + 1);
    }

    if (currentPage <= 3) {
      pages.push(1, 2, 3, "...", totalPages);
    } else if (currentPage >= totalPages - 2) {
      pages.push(1, "...", totalPages - 2, totalPages - 1, totalPages);
    } else {
      pages.push(1, "...", currentPage - 1, currentPage, currentPage + 1, "...", totalPages);
    }

    return pages;
  };

  const handleClick = (pageNumber) => {
    if (pageNumber !== "..." && pageNumber > 0 && pageNumber <= totalPages) {
      setCurrentPage(pageNumber);
    }
  };
  return (
    <div className="w-full lg:w-[60%] main-content mx-auto">
      <h1 className="font-bold uppercase wallet">My Wallet</h1>
      <p className="wallet-description">
        The Majestic Mall Wallet is a pre-paid credit account that is
        associated with your Majestic Mall account. This prepaid
        account allows you to pay a lump sum amount once to Majestic Mall and then shop multiple times without having to pay each time.
      </p>
      <div className="balance-section">
        <p>Current Balance: <span className="font-semibold">Rs.{balance?.toFixed(2)}</span></p>
        <button className="font-bold uppercase topup-btn" onClick={openModal}>
          Topup Your Wallet
        </button>
      </div>
      <h2 className="my-3 font-bold">Wallet Activity</h2>
      <div className="w-full overflow-x-auto">
        <table className="wallet-activity">
          <thead>
            <tr>
              <th>SR NO.</th>
              <th>TRANSACTION ID</th>
              <th>DATE</th>

              <th>TRANSACTION DETAIL</th>
              <th>CREDIT</th>
              <th>DEBIT</th>
            </tr>
          </thead>
          <tbody>
            {currentItems && currentItems.length > 0 ? (
              <>
                {[...currentItems]?.map((transaction, index) => (
                  <tr key={transaction?._id}>
                    <td>{index + 1}</td>
                    <td>ABHI{transaction?._id?.slice(20, 25)}</td>
                    <td>
                      {transaction?.date
                        ? new Date(transaction?.date).toLocaleDateString()
                        : "N/A"}
                    </td>

                    <td>{transaction?.detail}</td>
                    <td className="text-sm text-center text-[#000]">{transaction?.credit ? `Rs.${transaction?.credit}` : "-"}</td>
                    <td className="text-sm text-center text-red-600">{transaction?.debit ? `Rs.${transaction?.debit}` : "-"}</td>
                  </tr>
                ))}
              </>
            ) : (
              <tr>
                <td colSpan="6" className="text-center">
                  No transactions found
                </td>
              </tr>
            )}
          </tbody>
        </table>
      </div>
      {currentItems && currentItems.length > 0 ? (<div className="flex items-center justify-center">
        <ul className="flex items-center justify-center mx-2 mt-3 lg:mx-4 next">
          <li
            className="border border-#f7ab27-400 rounded-lg cursor-pointer lg:px-4 mx-1 px-2 py-2"
            style={{ color: currentPage === 1 ? "#d1d5db" : "#45595b" }}
            onClick={() => handleClick(currentPage - 1)}
            disabled={currentPage === 1}
          >
            &#8656;
          </li>
          {getVisiblePages().map((page, index) => (
            <li
              key={index}
              className={`py-2 px-2 lg:px-4 border border-#f7ab27-400 rounded-lg cursor-pointer ${currentPage === page ? "bg-[#000] text-white" : "hover:bg-#f7ab27-200"
                }`}
              onClick={() => handleClick(page)}
            >
              {page}
            </li>
          ))}

          <li
            className="border border-#f7ab27-400 rounded-lg cursor-pointer lg:px-4 mx-1 px-2 py-2"
            style={{
              color: currentPage === totalPages ? "#d1d5db" : "#45595b",
            }}
            onClick={() => handleClick(currentPage + 1)}
            disabled={currentPage === totalPages}
          >
            &#8658;
          </li>
        </ul>
      </div>) : null}

      {isModalOpen && (
        <TopupModal
          closeModal={closeModal}
          setBalance={setBalance}
          setTransactions={setTransactions}
        />
      )}
    </div>
  );
}

function TopupModal({ closeModal, setBalance, setTransactions }) {
  const [amount, setAmount] = useState("");
  const encryptionKey = process.env.REACT_APP_ENCRYPTION_KEY;
  const encryptedToken = Cookies.get("token");
  const token = encryptedToken
    ? CryptoJS.AES.decrypt(encryptedToken, encryptionKey).toString(
      CryptoJS.enc.Utf8
    )
    : null;
  const encryptedUser = Cookies.get("user");
  const user = encryptedUser
    ? JSON.parse(
      CryptoJS.AES.decrypt(encryptedUser, encryptionKey).toString(
        CryptoJS.enc.Utf8
      )
    )
    : null;

  const handleSubmit = async (event) => {
    event.preventDefault();

    if (!token) {
      message.error("Please login!");
      return;
    }
    const topupAmount = parseFloat(amount);

    if (!amount || topupAmount <= 0) {
      message.error("Please enter a valid amount.");
      return;
    }

    if (topupAmount < 300) {
      message.error("The minimum top-up amount is Rs. 300.");
      return;
    }
    try {
      const config = {
        url: "/user/topup",
        method: "post",
        baseURL: "https://majesticmall.net/api",
        hearder: { "content-type": "application/json" },
        headers: {
          Authorization: `Bearer ${token}`,
        },
        data: {
          userId: user?._id,
          amount: topupAmount,
        },
      };


      const response = await axios.post("https://majesticmall.net/api/user/addPaymentPhone",
        { userId: user?._id, username: user?.name, Mobile: user?.mobile, orderId: "Wallet" + Math.random(0, 10), amount: topupAmount, config: JSON.stringify(config), redirecturl: "/wallet", callBack: "/wallet" },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );
      if (response.status == 200) {
        return window.location.assign(response.data?.url?.url);
      }
    } catch (error) {
      console.error("Error topping up the wallet:", error);
      message.error("Error during top-up. Please try again.");
    } finally {
      closeModal();
    }
  };

  return (
    <div className="modal2-overlay">
      <ToastContainer />
      <ScrollToTop />
      <div className="modal2">
        <h2>Topup Your Wallet</h2>
        <form onSubmit={handleSubmit}>
          <label htmlFor="amount">Enter Amount (Rs):</label>
          <input
            type="number"
            id="amount"
            value={amount}
            onChange={(e) => setAmount(e.target.value)}
            min="1"
            required
          />
          <p className="bg-[#be343b] text-center text-sm text-white mt-2 py-2">
            Minimum recharge amount is Rs. 300
          </p>
          <div className="modal2-buttons">
            <button type="submit">Add Money</button>
            <button type="button" onClick={closeModal}>
              Cancel
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}
